              })
            })
            this.upstreamOptArr=upstreamList;
          }else{
            this.$message.error('操作失败: '+ res.data.data.message);
          }
          this.getUpstreamLoading=false;
        }).catch((err)=>{
          console.log(err)
        });
      }
