<!-- location自定义指令 -->
<template>
  <div>
    <el-dialog width="55%" :title="domainObj.domainName+':'+domainObj.port+' '+locationObj.nameTxt" :visible.sync="isShow" :close-on-click-modal="false" :before-close="directiveDialogClose">
      <div class="directiveBox">
        <el-button class="addDirectiveBtn" type="primary" size="mini" @click="addDctDialogShow()">新增自定义配置</el-button>
        <h1 class="directiveTitle">自定义配置</h1>
        <p class="nodata-box" v-show="directiveData.length==0">{{$t('lg.nodata')}}</p>
        <!-- {{directiveData}} -->
        <table class="table-bg" v-show="directiveData.length>0">
          <thead>
            <tr>
              <th>配置名称</th>
              <th>配置参数</th>
              <th width="110"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in directiveData">
              <td v-for="(vitem,key,index) in item">{{vitem}}</td><!--v-for遍历对象-->
              <td class="text-center">
                <a href="javascript:;" class="edit-del-a" @click="editDctDialogShow(item)"><i class="el-icon-edit-outline"></i>{{$t('lg.edit')}}</a>
                <a href="javascript:;" class="edit-del-a" @click="deletDirective(item)"><i class="el-icon-close"></i>{{$t('lg.delete')}}</a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <loading v-show="getDirectiveLoading"></loading>
    </el-dialog>

    <!-- 增加指令弹框 -->
    <el-dialog width="65%" :title="domainObj.domainName+':'+domainObj.port+' '+locationObj.nameTxt" :visible.sync="addDctDialogVisible" :close-on-click-modal="false" :before-close="addDctDialogClose">
      <el-row>
        <el-col :span="12">
          <el-form ref="directiveform" :model="directiveform" :rules="directiveformRules" label-width="80px" style="margin-right:30px">
            <el-form-item label="配置名称" prop="instructName">
              <el-input v-model.trim="directiveform.instructName" placeholder="请输入配置名称"></el-input>
            </el-form-item>
            <el-form-item label="配置参数" prop="instructParam">
              <el-input v-model.trim="directiveform.instructParam" placeholder="请输入配置参数"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="addDirectiveList('directiveform')">{{$t('lg.addconfig')}}</el-button>
              <el-button @click="addDctDialogClose">{{$t('lg.cancel')}}</el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="12">
          <div>
            <!-- <h1>预览</h1> -->
            <p class="nodata-box" v-show="directiveList.length==0">{{$t('lg.nodata')}}</p>
            <div v-show="directiveList.length>0" class="directiveListBox">
              <table>
                <thead>
                  <tr>
                    <th>配置名称</th>
                    <th>配置参数</th>
                    <th width="44"></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in directiveList">
                    <td>{{item.instructName}}</td>
                    <td>{{item.instructParam}}</td>
                    <td class="text-center font-red fsize-12 cursor-pointer" @click="delDirectiveList(item)">{{$t('lg.delete')}}</td>
                  </tr>
                </tbody>
              </table>
              <div class="margin-tb text-right"><el-button type="primary" @click="submitDirective">{{$t('lg.confirm')}}</el-button></div>
            </div>
          </div>
        </el-col>
      </el-row>
      <loading v-show="addDctLoading"></loading>
    </el-dialog>

    <!-- 编辑指令弹框 -->
    <el-dialog width="55%" :title="domainObj.domainName+':'+domainObj.port+' '+locationObj.nameTxt" :visible.sync="editDctDialogVisible" :close-on-click-modal="false" :before-close="editDctDialogClose">
      <el-form ref="editDirectiveform" :model="editDirectiveform" :rules="editDirectiveformRules" label-width="80px">
        <el-form-item label="配置名称" prop="instructName">
          <el-input v-model.trim="editDirectiveform.instructName" disabled></el-input>
        </el-form-item>

        <el-form-item label="配置参数" prop="instructParam">
          <el-input v-model.trim="editDirectiveform.instructParam"></el-input>
        </el-form-item>
      
        <el-form-item>
          <el-button type="primary" @click="editDctEnsure('editDirectiveform')">{{$t('lg.save')}}</el-button>
          <el-button @click="editDctDialogClose">{{$t('lg.back')}}</el-button>
        </el-form-item>
      </el-form>
      <loading v-show="editDctLoading"></loading>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios';
import * as types from '@/store/types';
import loading from '@/components/loading/loading';
import {SERVER_API_URL} from '@/conf/constvar';
import getLocationInstruct from './lc_instruct.js';

export default {
  components:{
    loading
  },
  props:{
    isAuto:{
      type:Boolean
    },
    isEditConfig:{
      type:Boolean
    }, 
    isShow:{
      type:Boolean
    },
    shadowObj:{
      type:Object
    },
    domainObj:{
      type:Object
    },
    locationObj:{
      type:Object
    }
  },
  data(){
    return{
      url:'',//请求路径
      getDirectiveLoading:false,
      directiveData:[],

      //增加
      addDctDialogVisible:false,
      directiveform:{
        instructName:'',
        instructParam:'',
      },
      directiveformRules:{
        instructName:[ 
          {required: true, message: this.$t('lg.required'), trigger: 'blur'},
        ],
      },
      directiveList:[],
      addDctLoading:false,
      
      //编辑
      editDctDialogVisible:false,
      editDirectiveform:{
        instructName:'',
        instructParam:'',
      },
      editDirectiveformRules:{
        instructName:[ 
          {required: true, message: this.$t('lg.required'), trigger: 'blur'},
        ],
      },
      editDirectiveOrgin:{//编辑服务器临时容器,被编辑的当前项
        instructName:'',
        instructParam:'',
      },
      editDctLoading:false,
    }
  },
  mounted(){
    if(this.isAuto){
      this.url=SERVER_API_URL+"pe/shadow/domain/location/directive?autoconfig=true";
    }else if(this.isEditConfig){
      this.url=SERVER_API_URL+"pe/shadow/domain/location/directive?savedConfig=true";
    }else{
      this.url=SERVER_API_URL+"pe/shadow/domain/location/directive";
    }
  },
  methods:{
    getLcCustomInstructData(){//获取location的特殊保护指令
      return new Promise((resolve,reject)=>{
        getLocationInstruct(this,{shadowObj:this.shadowObj,domainObj:this.domainObj,locationObj:this.locationObj},false,'shadow_custom',this.isAuto,this.isEditConfig).then((resolvedata)=>{//请求接口 异步 这是调用子组件里的方法
          // console.log(resolvedata);
          this.directiveData=resolvedata;
          resolve(resolvedata);
        });
      })
    },

    directiveDialogClose(){
      this.$emit("setbylcCustomInstructmodal",false);
    },

    //增加
    addDctDialogShow(){//显示添加弹框
      this.addDctDialogVisible=true;
    },
    addDirectiveList(formname){//添加到预览列表 保护配置
      this.$refs[formname].validate((valid) => {
        if (valid) {
          const instrName=this.directiveform.instructName,
                instrParam=this.directiveform.instructParam;

          const cmdData={
                  // "subfilterType":instrName,
                  "instructName":instrName,
                  "instructParam":instrParam
                };
          this.directiveList.push(cmdData);
        }
      });
    },
    delDirectiveList(item){//从预览表格里删除
      this.directiveList.splice(this.directiveList.indexOf(item),1);
    },
    submitDirective(){//确认添加
      const _this=this;
      _this.addDctLoading=true;
      let directivesArr=[];

      // console.log(this.directiveList);
      this.directiveList.forEach(ele=>{
        let str=ele['instructParam'];
        str=Base64.encode(str);
        directivesArr.push({
          [ele.instructName]:str
        })
      })

      // console.log(directivesArr)
      axios.post(_this.url,{
        snodeId:_this.shadowObj.snodeId,
        domainName:_this.domainObj.domainName,
        domainPort:_this.domainObj.port,
        locationName:_this.locationObj.name,
        locationType:_this.locationObj.type,
        directives:directivesArr
      }).then((res)=>{
        if(res.data.responseCode==0){
          _this.$message({
            type: 'success',
            message: _this.$t('lg.success')
          });
          _this.getLcCustomInstructData();
          _this.addDctDialogClose();
        }else{
          _this.$message.error(_this.$t('lg.failed')+'。输入的指令不匹配，请核对输入内容！'+ res.data.data.message);
        }
        _this.addDctLoading=false;
      }).catch((err)=>{
        console.log(err);
      })
    },
    addDctDialogClose(){//关闭添加指令弹框
      this.directiveList=[];
      this.$refs['directiveform'].resetFields();
      this.directiveform.raeChecklist=[this.$t('lg.encryptpa')];
      this.addDctDialogVisible=false;
    },

    //编辑
    editDctDialogShow(item){//打开编辑弹框 获取编辑参数
      // console.log(item)
      this.editDctDialogVisible=true;

      this.editDirectiveOrgin.instructName=item.customName;//保存改之前的旧数据用于发送请求
      this.editDirectiveOrgin.instructParam=item.customParams;

      this.editDirectiveform.instructName=item.customName;
      this.editDirectiveform.instructParam=item.customParams;
    },
    editDctEnsure(formname){//确认修改
      this.$refs[formname].validate((valid) => {
        if (valid) {
          const _this=this;
          _this.editDctLoading=true;

          var srcdictatedata = "";
          var dstdictatedata= "";
          if(this.editDirectiveOrgin.instructParam!=""){
            srcdictatedata=Base64.encode(this.editDirectiveOrgin.instructParam);
          }
          if(this.editDirectiveform.instructParam!=""){
            dstdictatedata=Base64.encode(this.editDirectiveform.instructParam);
          }

          axios.patch(_this.url,{
            snodeId:_this.shadowObj.snodeId,
            domainName:_this.domainObj.domainName,
            domainPort:_this.domainObj.port,
            locationName:_this.locationObj.name,
            locationType:_this.locationObj.type,
            directiveName:_this.editDirectiveOrgin.instructName,
            srcData:srcdictatedata,
            targetData:dstdictatedata,
          }).then((res)=>{
            if(res.data.responseCode==0){
              _this.$message({
                type: 'success',
                message: _this.$t('lg.success')
              });
              _this.getLcCustomInstructData();
              _this.editDctDialogClose();
            }else{
              _this.$message.error(_this.$t('lg.failed')+'。输入的指令不匹配，请核对输入内容！'+ res.data.data.message);
            }
            _this.editDctLoading=false;
          }).catch((err)=>{
            console.log(err);
          })
        }
      })
    },
    editDctDialogClose(){//关闭编辑指令弹框
      this.editDirectiveform.raeChecklist=[];
      this.$refs['editDirectiveform'].resetFields();
      this.editDctDialogVisible=false;
    },

    //删除
    deletDirective(item){
      // console.log(item)
      const _this=this;
      _this.getDirectiveLoading=true;

      const delItem = Object.assign([],item);//克隆对象  delItem=item;改变了原对象
      const srcdictateType=delItem.customName;
      var basevitem=Base64.encode(delItem.customParams);

      var directivesObj = {}, 
          directivesArr = [];
      directivesObj[srcdictateType]=basevitem;
      directivesArr.push(directivesObj);

      // console.log(directivesArr)
      axios.delete(_this.url,{
        data:{
          snodeId:_this.shadowObj.snodeId,
          domainName:_this.domainObj.domainName,
          domainPort:_this.domainObj.port,
          locationName:_this.locationObj.name,
          locationType:_this.locationObj.type,
          directives:directivesArr
        }
      }).then((res)=>{
        // console.log(res);
        if(res.data.responseCode==0){
          _this.$message({
            type: 'success',
            message: _this.$t('lg.success')
          });
          _this.getLcCustomInstructData();
        }else{
          _this.$message.error(_this.$t('lg.failed')+':'+ res.data.data.message);
        }
        _this.getDirectiveLoading=false;
      }).catch((err)=>{
        console.log(err)
      })
    },
  },
}
</script>

<style scoped>
.directiveBox{
  background: #f7f7f7;
  padding: 12px;
  position: relative;
}
.addDirectiveBtn{
  position: absolute;
  right: 12px;
  top: 12px;
}
.directiveTitle{
  font-size: 18px; 
  color: #333; 
  margin:5px 0 15px;
}
/* .directiveListBox{
  max-height: 300px;
  overflow-y: auto;
} */
</style>


