<!-- location特殊保护指令 -->
<template>
  <div>
    <el-dialog width="70%" :title="domainObj.domainName+':'+domainObj.port+' '+locationObj.nameTxt" :visible.sync="isShow" :close-on-click-modal="false" :before-close="directiveDialogClose">
      <div class="directiveBox">
        <el-button class="addDirectiveBtn" type="primary" size="mini" @click="addDctDialogShow()">新增特殊保护配置</el-button>
        <h1 class="directiveTitle">{{$t('lg.spconfig')}}</h1>
        <p class="nodata-box" v-show="directiveData.length==0">{{$t('lg.nodata')}}</p>
        <!-- {{directiveData}} -->
        <table class="table-bg" v-show="directiveData.length>0">
          <thead>
            <tr>
              <th>{{$t('lg.url3')}}</th>
              <th>{{$t('lg.remained')}}</th>
              <th>{{$t('lg.url2')}}</th>
              <th>{{$t('lg.validdate')}}</th>
              <th>{{$t('lg.allowedrefreshes')}}</th>
              <th>{{$t('lg.configproperty')}}</th>
              <th width="110"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in directiveData">
              <td>{{item.srcUri}}</td>
              <td>{{item.retainPart}}</td>
              <td>{{item.jumpUrl}}</td>
              <td>{{item.timeout}}</td>
              <td>{{item.refreshNum}}</td>
              <td>{{item.confType|toConftype}}</td>
              <td class="text-center">
                <a href="javascript:;" class="edit-del-a" @click="editDctDialogShow(item)"><i class="el-icon-edit-outline"></i>{{$t('lg.edit')}}</a>
                <a href="javascript:;" class="edit-del-a" @click="deletDirective(item)"><i class="el-icon-close"></i>{{$t('lg.delete')}}</a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <loading v-show="getDirectiveLoading"></loading>
    </el-dialog>

    <!-- 增加指令弹框 -->
    <el-dialog width="65%" :title="domainObj.domainName+':'+domainObj.port+' '+locationObj.nameTxt" :visible.sync="addDctDialogVisible" :close-on-click-modal="false" :before-close="addDctDialogClose">
          <el-form ref="directiveform" :model="directiveform" :rules="directiveformRules" label-width="110px" style="margin-right:30px">
            <el-form-item :label="$t('lg.ourl')" prop="uri1">
              <el-input v-model.trim="directiveform.uri1"></el-input>
            </el-form-item>
            <el-form-item :label="$t('lg.remained')" prop="uri2">
              <el-input v-model.trim="directiveform.uri2"></el-input>
            </el-form-item>
            <el-form-item :label="$t('lg.url2')" prop="uri3">
              <el-input v-model.trim="directiveform.uri3"></el-input>
            </el-form-item>
            <el-form-item :label="$t('lg.timeoutset')" prop="time">
              <el-input v-model.trim="directiveform.time" style="width:40%"></el-input>
              <el-select v-model="overTimeValue" placeholder="$t('lg.select')" style="width:30%">
                <el-option
                  v-for="item in overTimeUnit"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item :label="$t('lg.allowedrefreshes')" prop="refreshtimes">
              <el-input v-model.trim="directiveform.refreshtimes"></el-input>
            </el-form-item>
            <el-form-item :label="$t('lg.configproperty')">
              <div class="checkBorder">
                <el-checkbox-group v-model="directiveform.raeChecklist" size="mini">
                  <el-checkbox :label="$t('lg.encrypt2')"></el-checkbox>
                  <el-checkbox :label="$t('lg.encryptab')"></el-checkbox>
                  <el-checkbox :label="$t('lg.encryptpa')" disabled></el-checkbox>
                  <el-checkbox label="允许访问原URI"></el-checkbox>
                </el-checkbox-group>
              </div>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="addDirectiveList('directiveform')">增加</el-button>
              <el-button @click="addDctDialogClose">{{$t('lg.cancel')}}</el-button>
            </el-form-item>
          </el-form>
      <loading v-show="addDctLoading"></loading>
    </el-dialog>

    <!-- 编辑指令弹框 -->
    <el-dialog width="55%" :title="domainObj.domainName+':'+domainObj.port+' '+locationObj.nameTxt" :visible.sync="editDctDialogVisible" :close-on-click-modal="false" :before-close="editDctDialogClose">
      <el-form ref="editDirectiveform" :model="editDirectiveform" :rules="editDirectiveformRules" label-width="110px">
        <el-form-item :label="$t('lg.ourl')" prop="uri1">
          <el-input v-model.trim="editDirectiveform.uri1"></el-input>
        </el-form-item>

        <el-form-item :label="$t('lg.remained')" prop="uri2">
          <el-input v-model.trim="editDirectiveform.uri2"></el-input>
        </el-form-item>

        <el-form-item :label="$t('lg.url2')" prop="uri3">
          <el-input v-model.trim="editDirectiveform.uri3"></el-input>
        </el-form-item>
        
        <el-form-item :label="$t('lg.timeoutset')" prop="timeNum">
          <el-input v-model.trim="editDirectiveform.timeNum" style="width:30%"></el-input>
          <el-select v-model="overTimeValue" :placeholder="$t('lg.select')" style="width:20%">
            <el-option
              v-for="item in overTimeUnit"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('lg.allowedrefreshes')" prop="refreshtimes">
          <el-input v-model.trim="editDirectiveform.refreshtimes"></el-input>
        </el-form-item>
        <el-form-item :label="$t('lg.configproperty')">
          <div class="checkBorder">
            <el-checkbox-group size="mini" v-model="editDirectiveform.raeChecklist">
              <el-checkbox :label="$t('lg.encrypt2')"></el-checkbox>
              <el-checkbox :label="$t('lg.encryptab')"></el-checkbox>
              <el-checkbox :label="$t('lg.encryptpa')" disabled></el-checkbox>
              <el-checkbox label="允许访问原URI"></el-checkbox>
            </el-checkbox-group>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="editDctEnsure('editDirectiveform')">{{$t('lg.save')}}</el-button>
          <el-button @click="editDctDialogClose">{{$t('lg.back')}}</el-button>
        </el-form-item>
      </el-form>
      <loading v-show="editDctLoading"></loading>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios';
import * as types from '@/store/types';
import loading from '@/components/loading/loading';
import {SERVER_API_URL} from '@/conf/constvar';
import getLocationInstruct from './lc_instruct.js';
import {handleConfigure} from '@/assets/js/tool';

export default {
  components:{
    loading
  },
  props:{
    isAuto:{
      type:Boolean
    },
    isEditConfig:{
      type:Boolean
    }, 
    isShow:{
      type:Boolean
    },
    shadowObj:{
      type:Object
    },
    domainObj:{
      type:Object
    },
    locationObj:{
      type:Object
    }
  },
  data(){
    return{
      url:'',//请求路径
      getDirectiveLoading:false,
      directiveData:[],

      //编辑、增加公用
      overTimeUnit:[{
        value: 'y',
        label: this.$t('lg.year')
      },{
        value: 'M',
        label: this.$t('lg.month')
      },{
        value: 'w',
        label: this.$t('lg.week')
      },{
        value: 'd',
        label: this.$t('lg.day')
      },{
        value: 'h',
        label: this.$t('lg.hour')
      },{
        value: 'm',
        label: this.$t('lg.minite')
      },{
        value: 's',
        label: this.$t('lg.second')
      },{
        value: 'ms',
        label: this.$t('lg.ms')
      }],
      overTimeValue:'s',

      //增加
      addDctDialogVisible:false,
      directiveform:{
        uri1:'',
        uri2:'',
        uri3:'',
        time:500,
        refreshtimes:1,
        raeChecklist:[this.$t('lg.encryptpa')]
      },
      directiveformRules:{
        uri1:[ 
          {required: true, message: this.$t('lg.required'), trigger: 'blur'},
        //  {pattern:/^([\w\-." \\*!$@?^()<>{}[\]=%&:;/~+#`,])+$/, message: this.$t('lg.invalidformat'), trigger: 'blur'} //不能输入中文、单引号及少部分特殊字符 有空格
        ],
        uri2:[
          // {required:true, message:this.$t('lg.required'), trigger:'blur'},
        //  {pattern:/^([\w\-." \\*!$@?^()<>{}[\]=%&:;/~+#`,])+$/, message:this.$t('lg.invalidformat'), trigger: 'blur'} //不能输入中文、单引号及少部分特殊字符 有空格
        ],
        uri3:[
          {required:true, message:this.$t('lg.required'), trigger:'blur'},
         // {pattern:/^([\w\-." \\*!$@?^()<>{}[\]=%&:;/~+#`,])+$/, message: this.$t('lg.invalidformat'), trigger: 'blur'} //不能输入中文、单引号及少部分特殊字符 有空格
        ],
        time:[
          {required:true, message:this.$t('lg.required'), trigger:'blur'},
          {pattern:/^[0-9]+$/,message: this.$t('lg.requirepositive'), trigger: 'change'},
        ],
        refreshtimes:[
          {required:true, message:this.$t('lg.required'), trigger:'blur'},
          {pattern:/^[0-9]+$/,message: this.$t('lg.requirepositive'), trigger: 'change'},
        ]
      },
      directiveList:[],
      addDctLoading:false,
      
      //编辑
      editDctDialogVisible:false,
      editDirectiveform:{
        uri1:'',
        uri2:'',
        uri3:'',
        time:'',
        timeNum:null,
        refreshtimes:'',
        raeChecklist:[]
      },
      editDirectiveformRules:{
        uri1:[ 
          {required: true, message: this.$t('lg.required'), trigger: 'blur'},
         // {pattern:/^([\w\-." \\*!$@?^()<>{}[\]=%&:;/~+#`,])+$/, message: this.$t('lg.invalidformat'), trigger: 'blur'} //不能输入中文、单引号及少部分特殊字符 有空格
        ],
        uri2:[
          // {required:true, message:this.$t('lg.required'), trigger:'blur'},
         // {pattern:/^([\w\-." \\*!$@?^()<>{}[\]=%&:;/~+#`,])+$/, message: this.$t('lg.invalidformat'), trigger: 'blur'} //不能输入中文、单引号及少部分特殊字符 有空格
        ],
        uri3:[
          {required:true, message:this.$t('lg.required'), trigger:'blur'},
         // {pattern:/^([\w\-." \\*!$@?^()<>{}[\]=%&:;/~+#`,])+$/, message: this.$t('lg.invalidformat'), trigger: 'blur'} //不能输入中文、单引号及少部分特殊字符 有空格
        ],
        timeNum:[
          {required:true, message:this.$t('lg.required'), trigger:'blur'},
          {pattern:/^[0-9]+$/,message: this.$t('lg.requirepositive'), trigger: 'change'},
        ],
        refreshtimes:[
          {required:true, message:this.$t('lg.required'), trigger:'blur'},
          {pattern:/^[0-9]+$/,message: this.$t('lg.requirepositive'), trigger: 'change'},
        ]
      },
      editDirectiveOrgin:{//编辑服务器临时容器,被编辑的当前项
        uri1:'',
        uri2:'',
        time:'',
        refreshtimes:1,
        raeChecklist:[]
      },
      orginEditdirective:{},
      editDctLoading:false,
    }
  },
  mounted(){
    if(this.isAuto){
      this.url=SERVER_API_URL+"pe/shadow/domain/location/protectconf/special?autoconfig=true";
    }else if(this.isEditConfig){
      this.url=SERVER_API_URL+"pe/shadow/domain/location/protectconf/special?savedConfig=true";
    }else{
      this.url=SERVER_API_URL+"pe/shadow/domain/location/protectconf/special";
    }
  },
  methods:{
    getLcSpecialProtectInstructData2(){//获取location的特殊保护指令
      return new Promise((resolve,reject)=>{
        getLocationInstruct(this,{shadowObj:this.shadowObj,domainObj:this.domainObj,locationObj:this.locationObj},false,'shadow_sub_filter_ex',this.isAuto,this.isEditConfig).then((resolvedata)=>{//请求接口 异步 这是调用子组件里的方法
          this.directiveData=resolvedata;
          resolve(resolvedata);
        });
      })
    },
    getLcSpecialProtectInstructData(){//获取location的保护指令
      return new Promise((resolve,reject)=>{
        const _this=this;
        axios.get(_this.url,{
          params: {
            "snodeId": _this.shadowObj.snodeId,
            "domainName": _this.domainObj.domainName,
            "domainPort": _this.domainObj.port,
            "locationName": _this.locationObj.name,
            "locationType": _this.locationObj.type
          }
        }).then((res)=>{
            //console.log(res)
            let resolvedata=res.data.data || [];
           // if(resolvedata){
              this.directiveData=resolvedata;
           // }    
            resolve(resolvedata)
        })
      })
    },

    directiveDialogClose(){
      this.$emit("setbylcSpecialProtectInstructmodal",false);
    },

    //增加
    addDctDialogShow(){//显示添加弹框
      this.addDctDialogVisible=true;
    },
    addDirectiveList(formname){//添加到预览列表 特殊保护配置
    const _this=this;
      this.$refs[formname].validate((valid) => {
        if (valid) {
          const ee=this.$t('lg.encrypt2'),//rr=this.$t('lg.replaceconfig'),aa=this.$t('lg.allowaccessourl'),
                ss=this.$t('lg.encryptab'),
                aa='允许访问原URI',
                pp=this.$t('lg.encryptpa');

          const uri1=this.directiveform.uri1,
                uri2=this.directiveform.uri2,
                uri3=this.directiveform.uri3,
                time=this.directiveform.time+this.overTimeValue,
                refreshtimes=this.directiveform.refreshtimes,
                raeChecklist=this.directiveform.raeChecklist;

          const getARE=raeChecklist.join("").replace(ee,"e").replace(ss,'s').replace(aa,'a').replace(pp,'p');

          // const cmdData={
          //       "subfilterType":"shadow_sub_filter_ex",
          //       "uri1":uri1,
          //       "uri2":uri2,
          //       "uri3":uri3,
          //       "time":time,
          //       "refreshtimes":refreshtimes,
          //       "oarj":getARE
          //     };
          // this.directiveList.push(cmdData);
let cmdData={
             "domainName": _this.domainObj.domainName,
              "domainPort": _this.domainObj.port,
              "locationName": _this.locationObj.name,
              "locationType": _this.locationObj.type,
              "snodeId": _this.shadowObj.snodeId,
              "srcUri": uri1,
              "retainPart":uri2,
              "jumpUrl": uri3,
              "timeout": time,
              "refreshNum": refreshtimes,
              "confType": getARE
          };
        axios.post(_this.url,cmdData).then((res)=>{
          if(res.data.responseCode==0){
          _this.$message({
            type: 'success',
            message: _this.$t('lg.success')
          });
          _this.getLcSpecialProtectInstructData();
          _this.$emit("updateRenderTree");
          _this.addDctDialogClose();
        }else{
          _this.$message.error(_this.$t('lg.failed')+'。输入的指令不匹配，请核对输入内容！'+ res.data.data.message);
        }
        _this.addDctLoading=false;
        }).catch((err)=>{
          console.log(err);
        })


        }
      });
    },
    delDirectiveList(item){//从预览表格里删除
      this.directiveList.splice(this.directiveList.indexOf(item),1);
    },
    submitDirective(){//确认添加
      const _this=this;
      _this.addDctLoading=true;
      let directivesArr=[];

      // console.log(this.directiveList);
      this.directiveList.forEach(ele=>{
        const oarjV=ele['oarj'];
        let str="";
        if(oarjV==""){//不传入配置属性时 
          // str="'"+ele['uri1']+"'"+" "+"'"+ele['uri2']+"'"+" "+"'"+ele['uri3']+"'"+" "+ele['time']+" "+ele['refreshtimes'];  //一定会传
        }else{
          str=ele['uri1']+" "+ele['uri2']+" "+ele['uri3']+" "+ele['time']+" "+ele['refreshtimes']+" "+oarjV;
        }
        str=Base64.encode(str);
        directivesArr.push({
          shadow_sub_filter_ex:str
        })
      })

      // console.log(directivesArr)
      axios.post(_this.url,{
        snodeId:_this.shadowObj.snodeId,
        domainName:_this.domainObj.domainName,
        domainPort:_this.domainObj.port,
        locationName:_this.locationObj.name,
        locationType:_this.locationObj.type,
        directives:directivesArr
      }).then((res)=>{
        if(res.data.responseCode==0){
          _this.$message({
            type: 'success',
            message: _this.$t('lg.success')
          });
          _this.getLcSpecialProtectInstructData();
          _this.$emit("updateRenderTree");
          _this.addDctDialogClose();
        }else{
          _this.$message.error(_this.$t('lg.failed')+'。输入的指令不匹配，请核对输入内容！'+ res.data.data.message);
        }
        _this.addDctLoading=false;
      }).catch((err)=>{
        console.log(err);
      })
    },
    addDctDialogClose(){//关闭添加指令弹框
      this.directiveList=[];
      this.$refs['directiveform'].resetFields();
      this.directiveform.raeChecklist=[this.$t('lg.encryptpa')];
      this.addDctDialogVisible=false;
    },
    editDctDialogShow(item){
      console.log(item)
      var _this=this;
      this.editDctDialogVisible=true;
      this.orginEditdirective=JSON.parse(JSON.stringify(item));
      //var rae=item.confType;
      const configureArr={//配置属性文本文字转换成code 
              '路径加密':'p',
              '允许访问原URI':'a',
              '二次加密':'e',
              '绝对路径':'s'
            }
      let timearr=item.timeout.match(/^(\d*)(\w*)/);
      let raearr=[];
      if(item.confType){
        raearr=item.confType.split("");
      }
      
      let raeck=raearr.map((item)=>{
        if(item=="p"){
          item="路径加密"
        }else if(item=="a"){
          item="允许访问原URI"
        }else if(item=="e"){
          item="二次加密"
        }else if(item=="s"){
          item="绝对路径"
        }
        return item;
      })
     // console.log(raearr)
      this.editDirectiveform.uri1=item.srcUri;
      this.editDirectiveform.uri2=item.retainPart;
      this.editDirectiveform.uri3=item.jumpUrl;
      this.editDirectiveform.refreshtimes=item.refreshNum;
      this.editDirectiveform.timeNum=timearr[1];
      this.editDirectiveform.overTimeValue=timearr[2];
      this.editDirectiveform.raeChecklist=raeck;

    },
    //编辑
    editDctDialogShow2(item){//打开编辑弹框 获取编辑参数
      // console.log(item)
      this.editDctDialogVisible=true;

      this.editDirectiveOrgin.uri1=item[0];//保存改之前的旧数据用于发送请求
      this.editDirectiveOrgin.uri2=item[1];
      this.editDirectiveOrgin.uri3=item[2];
      this.editDirectiveOrgin.refreshtimes=item[4];

      //处理配置属性
      var rae=item[5];
      const configureArr={//配置属性文本文字转换成code
              '二次加密':'e',
              '绝对路径':'s',
              '路径加密':'p',
              '允许访问原URI':'a'
            }
      this.editDirectiveOrgin.raeChecklist=handleConfigure(rae,configureArr,false);
      
      this.editDirectiveform.raeChecklist=[];
      this.editDirectiveform.raeChecklist=rae.split('、');

      //处理超时时间项的时间单位
      // console.log(item[3])
      // console.log(item[3].match(/(\d+)([\u4e00-\u9fa5]+)/)[2])
      switch(item[3].match(/(\d+)([\u4e00-\u9fa5]+)/)[2])
      {
        case "年":
          this.editDirectiveOrgin.time=item[3].match(/(\d*)(\w*)/)[1]+"y";
          this.editDirectiveform.time=item[3].match(/(\d*)(\w*)/)[1]+"y";
          break;
        case "月":
          this.editDirectiveOrgin.time=item[3].match(/(\d*)(\w*)/)[1]+"M";
          this.editDirectiveform.time=item[3].match(/(\d*)(\w*)/)[1]+"M";
          break;
        case "周":
          this.editDirectiveOrgin.time=item[3].match(/(\d*)(\w*)/)[1]+"w";
          this.editDirectiveform.time=item[3].match(/(\d*)(\w*)/)[1]+"w";
          break;
        case "天":
          this.editDirectiveOrgin.time=item[3].match(/(\d*)(\w*)/)[1]+"d";
          this.editDirectiveform.time=item[3].match(/(\d*)(\w*)/)[1]+"d";
          break;	
        case "小时":
          this.editDirectiveOrgin.time=item[3].match(/(\d*)(\w*)/)[1]+"h";
          this.editDirectiveform.time=item[3].match(/(\d*)(\w*)/)[1]+"h";
          break;
        case "分钟":
          this.editDirectiveOrgin.time=item[3].match(/(\d*)(\w*)/)[1]+"m";
          this.editDirectiveform.time=item[3].match(/(\d*)(\w*)/)[1]+"m";
          break;
        case "秒":
          this.editDirectiveOrgin.time=item[3].match(/(\d*)(\w*)/)[1]+"s";
          this.editDirectiveform.time=item[3].match(/(\d*)(\w*)/)[1]+"s";
          break;
        default:
          this.editDirectiveOrgin.time=item[3].match(/(\d*)(\w*)/)[1]+"ms";
          this.editDirectiveform.time=item[3].match(/(\d*)(\w*)/)[1]+"ms";
      };
      this.editDirectiveform.timeNum=this.editDirectiveform.time.match(/(\d*)(\w*)/)[1]-0;
      this.overTimeValue=this.editDirectiveform.time.match(/(\d*)(\w*)/)[2];

      this.editDirectiveform.uri1=item[0];
      this.editDirectiveform.uri2=item[1];
      this.editDirectiveform.uri3=item[2];
      this.editDirectiveform.refreshtimes=item[4];
    },
     editDctEnsure(formname){
      
//  console.log(this.orginEditdirective)
  const _this=this;
      this.$refs[formname].validate((valid) => {
 if (valid) {
    const pp="路径加密",
                aa="允许访问原URI",
                ee="二次加密",
                ss="绝对路径";
var raeChecklist=this.editDirectiveform.raeChecklist;
  raeChecklist=this.editDirectiveform.raeChecklist;
var getARE=raeChecklist.join("").replace(pp,"p").replace(aa,"a").replace(ee,"e").replace(ss,'s');
 axios.patch(_this.url,{
"domainName": _this.domainObj.domainName,
  "domainPort": _this.domainObj.port,
  "locationName": _this.locationObj.name,
  "locationType":_this.locationObj.type,
  "snodeId": _this.shadowObj.snodeId,
  "srcUri": this.editDirectiveform.uri1,
  "retainPart":this.editDirectiveform.uri2,
  "jumpUrl": this.editDirectiveform.uri3,
  "timeout": this.editDirectiveform.timeNum+this.overTimeValue,
  "refreshNum": this.editDirectiveform.refreshtimes,
  "confType": getARE,
  "oldSrcUri":this.orginEditdirective.srcUri,
  "oldJumpUrl": this.orginEditdirective.jumpUrl,
  "oldTimeout": this.orginEditdirective.timeout,
  "oldRefreshNum": this.orginEditdirective.refreshNum,
  "oldRetainPart":this.orginEditdirective.retainPart,
  "oldConfType": this.orginEditdirective.confType
          }).then((res)=>{
            if(res.data.responseCode==0){
              _this.$message({
                type: 'success',
                message: _this.$t('lg.success')
              });
              _this.getLcSpecialProtectInstructData();
              _this.$emit("updateRenderTree");
              _this.editDctDialogClose();
            }else{
              _this.$message.error(_this.$t('lg.failed')+':'+ res.data.data.message);
            }
            _this.editDctLoading=false;
          }).catch((err)=>{
            console.log(err);
          })     
      }
      }) 
     },
    editDctEnsure2(formname){//确认修改
      this.$refs[formname].validate((valid) => {
        if (valid) {
          const _this=this;
          _this.editDctLoading=true;
           
          const ee="二次加密", //rr="替换配置", aa="允许访问原URI",
                ss="绝对路径",
                pp="路径加密";
          var dstraeChecklist=this.editDirectiveform.raeChecklist.join("").replace(ee,"e").replace(ss,"s").replace(pp,"p");//.replace(rr,"r").replace(aa,"a")
          
          if(this.editDirectiveOrgin.raeChecklist==""){//不传入指令属性时 
            // var srcdictatedata=Base64.encode("'"+this.editDirectiveOrgin.uri1+"'"+" "+"'"+this.editDirectiveOrgin.uri2+"'"+" "+"'"+this.editDirectiveOrgin.uri3+"'"+" "+this.editDirectiveOrgin.time+" "+this.editDirectiveOrgin.refreshtimes);
          }else{
            var srcdictatedata=Base64.encode(this.editDirectiveOrgin.uri1+" "+this.editDirectiveOrgin.uri2+" "+this.editDirectiveOrgin.uri3+" "+this.editDirectiveOrgin.time+" "+this.editDirectiveOrgin.refreshtimes+" "+this.editDirectiveOrgin.raeChecklist);
          }

          if(dstraeChecklist==""){//不传入指令属性时 
            // var dstdictatedata=Base64.encode("'"+this.editDirectiveform.uri1+"'"+" "+"'"+this.editDirectiveform.uri2+"'"+" "+"'"+this.editDirectiveform.uri3+"'"+" "+this.editDirectiveform.timeNum+this.overTimeValue+" "+this.editDirectiveform.refreshtimes);
          }else{
            var dstdictatedata=Base64.encode(this.editDirectiveform.uri1+" "+this.editDirectiveform.uri2+" "+this.editDirectiveform.uri3+" "+this.editDirectiveform.timeNum+this.overTimeValue+" "+this.editDirectiveform.refreshtimes+" "+dstraeChecklist);
          }
         
          axios.patch(_this.url,{
            snodeId:_this.shadowObj.snodeId,
            domainName:_this.domainObj.domainName,
            domainPort:_this.domainObj.port,
            locationName:_this.locationObj.name,
            locationType:_this.locationObj.type,
            directiveName:'shadow_sub_filter_ex',
            srcData:srcdictatedata,
            targetData:dstdictatedata,
          }).then((res)=>{
            if(res.data.responseCode==0){
              _this.$message({
                type: 'success',
                message: _this.$t('lg.success')
              });
              _this.getLcSpecialProtectInstructData();
              _this.$emit("updateRenderTree");
              _this.editDctDialogClose();
            }else{
              _this.$message.error(_this.$t('lg.failed')+'。输入的指令不匹配，请核对输入内容！'+ res.data.data.message);
            }
            _this.editDctLoading=false;
          }).catch((err)=>{
            console.log(err);
          })
        }
      })
    },
    editDctDialogClose(){//关闭编辑指令弹框
      this.editDirectiveform.raeChecklist=[];
      this.$refs['editDirectiveform'].resetFields();
      this.editDctDialogVisible=false;
    },
deletDirective(item){
const _this=this;
      _this.getDirectiveLoading=true;
      axios.delete(_this.url,{
        data:{
          "domainName": _this.domainObj.domainName,
          "domainPort": _this.domainObj.port,
          "locationName": _this.locationObj.name,
          "locationType": _this.locationObj.type,
          "snodeId": _this.shadowObj.snodeId,
          "oldSrcUri": item.srcUri,
          "oldRetainPart":item.retainPart,
          "oldJumpUrl": item.jumpUrl,
          "oldTimeout": item.timeout,
          "oldRefreshNum": item.refreshNum,
          "oldConfType": item.confType
        }
      }).then((res)=>{
        // console.log(res);
          if(res.data.responseCode==0){
          _this.$message({
            type: 'success',
            message: _this.$t('lg.success')
          });
          _this.getLcSpecialProtectInstructData();
          _this.$emit("updateRenderTree");
        }else{
          _this.$message.error(_this.$t('lg.failed')+':'+ res.data.data.message);
        }
        _this.getDirectiveLoading=false;
      }).catch((err)=>{
        console.log(err)
      })
},
    //删除
    deletDirective2(item){
      // console.log(item)
      const _this=this;
      _this.getDirectiveLoading=true;

      const delItem = Object.assign([],item);//克隆对象  delItem=item;改变了原对象
      if(delItem[5]==""){//无指令属性时
        delItem.pop(delItem[5]);
      }else{
        const configureArr={//配置属性文本文字转换成code
                '二次加密':'e',
                '绝对路径':'s',
                '路径加密':'p'
              }
        delItem[5]=handleConfigure(delItem[5],configureArr,false);
      }
      switch(delItem[3].match(/(\d+)([\u4e00-\u9fa5]+)/)[2])
      {
        case "年":
          delItem[3]=delItem[3].match(/(\d*)(\w*)/)[1]+"y";
          break;
        case "月":
          delItem[3]=delItem[3].match(/(\d*)(\w*)/)[1]+"M";
          break;
        case "周":
          delItem[3]=delItem[3].match(/(\d*)(\w*)/)[1]+"w";
          break;
        case "天":
          delItem[3]=delItem[3].match(/(\d*)(\w*)/)[1]+"d";
          break;	
        case "小时":
          delItem[3]=delItem[3].match(/(\d*)(\w*)/)[1]+"h";
          break;
        case "分钟":
          delItem[3]=delItem[3].match(/(\d*)(\w*)/)[1]+"m";
          break;
        case "秒":
          delItem[3]=delItem[3].match(/(\d*)(\w*)/)[1]+"s";
          break;
        default:
          delItem[3]=delItem[3].match(/(\d*)(\w*)/)[1]+"ms";
      };

      var basevitem=Base64.encode(delItem.join(" ").replace(/^\s+|\s+$/gm,''));
      var directivesObj = {}, 
          directivesArr = [];

      directivesObj['shadow_sub_filter_ex']=basevitem;
      directivesArr.push(directivesObj);

      // console.log(directivesArr)
      axios.delete(_this.url,{
        data:{
          snodeId:_this.shadowObj.snodeId,
          domainName:_this.domainObj.domainName,
          domainPort:_this.domainObj.port,
          locationName:_this.locationObj.name,
          locationType:_this.locationObj.type,
          directives:directivesArr
        }
      }).then((res)=>{
        // console.log(res);
        if(res.data.responseCode==0){
          _this.$message({
            type: 'success',
            message: _this.$t('lg.success')
          });
          _this.getLcSpecialProtectInstructData();
          _this.$emit("updateRenderTree");
        }else{
          _this.$message.error(_this.$t('lg.failed')+':'+ res.data.data.message);
        }
        _this.getDirectiveLoading=false;
      }).catch((err)=>{
        console.log(err)
      })
    },
  },
    filters:{
    toConftype(item){
      if(item){
      return item.replace("e","二次加密；").replace("a","允许访问原URI；").replace("s","绝对路径；").replace("x","正则；").replace("s","绝对路径；").replace("p","路径加密；")
    }}
  }
}
</script>

<style scoped>
.addDirectiveBtn{
  position: absolute;
  right: 12px;
  top: 12px;
}
.directiveTitle{
  font-size: 18px; 
  color: #333; 
  margin:5px 0 15px;
}
.directiveBox{
  background: #f7f7f7;
  padding: 12px;
  position: relative;
}
</style>


