<template>
  <div>
    <el-dialog width="50%" :title="shadowObj.name+' ['+shadowObj.snodeId+']'" :visible.sync="pushmodalShow" :close-on-click-modal="false" :before-close="pushmodalResetForm">
      <el-form ref="pushmodalform" :model="pushmodalform" :rules="pushmodalRules"  label-width="105px">
        <el-form-item :label="$t('lg.shadownode')" prop="shadownodes">
          <el-select v-model="pushmodalform.shadownodes" placeholder="选择Shadow节点">
            <template v-for="item in data">
              <el-option :label="item.isShadowGroup?item.hostName+'(集群)':item.hostName+'('+item.snodeId+')'" :value="item.snodeId"></el-option>
            </template>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="pushmodalFn('pushmodalform','pushsome')">{{$t('lg.pushconfig')}}</el-button>
          <el-button type="primary" @click="pushmodalFn('pushmodalform','pushall')">{{$t('lg.pushcompletely')}}</el-button>
          <el-button @click="pushmodalResetForm">{{$t('lg.cancel')}}</el-button>
        </el-form-item>
      </el-form>
      <loading v-show="pushmodalLoading" :title="$t('lg.handling')"></loading>
    </el-dialog>
  </div>
</template>
<script>
import axios from 'axios'
import loading from '@/components/loading/loading';
import {SERVER_API_URL} from '@/conf/constvar'
export default{
  props:{
    data:{
      type:Array
    },
    pushmodalShow:{
      type:Boolean
    },
    shadowObj:{
      type:Object
    }
  },
  data(){
    return{
      pushmodalform:{
        shadownodes:this.data
      },
      pushmodalRules:{
        shadownodes:[{required: true, message: this.$t('lg.required'), trigger: 'change'}]
      },
      pushmodalLoading:false
    }
  },
  methods:{
    pushmodalResetForm(){
      this.$refs['pushmodalform'].resetFields();
      this.$emit("setbypushmodal",false)
    },
    pushmodalFn(formname,type){
      // console.log(type)
      var _this=this;
      this.$refs[formname].validate((valid) => {
        if(valid){
          this.pushmodalLoading=true;
          if(type=="pushsome"){
            var url=SERVER_API_URL+"pe/push";//推送配置
          }else{
            var url=SERVER_API_URL+"pe/shadow/config/push/folder";//完全推送
          }
          var srcId=this.shadowObj.snodeId;
          var dstId=this.pushmodalform.shadownodes;
          axios.post(url,{
            srcSnodeId:srcId,
            dstSnodeId:dstId
          }).then((res)=>{
            if(res.data.responseCode=="0"){
              this.$message({
                type: 'success',
                message:  _this.$t('lg.success')
              });
              this.$emit("updateRenderTree");
              this.pushmodalResetForm();
            }else{
              this.$message.error(_this.$t('lg.failed')+': '+res.data.data.message);
            }
            this.pushmodalLoading=false;
          }).catch((err)=>{
            console.log(err)
          });
        }
      });
    }
  },
  components:{
    loading
  }
}
</script>



